.about {
    max-width: 1140px;
    min-height: 608px;
    margin: 0 auto;
}

.about__title {
    margin: 110px auto 23px;
    padding: 0 0 23px;
    text-align: left;
    border-bottom: solid 1px white;
    color: #FFF;
    font-weight: 400;
    font-size: 22px;
    letter-spacing: -0.04em;
}

.about__info {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    margin: 24px 0 0 0;
    padding: 0;
}

.about__info-item-title {
    color: #FFF;
    font-weight: 400;
    letter-spacing: 0;
    font-size: 20px;
    letter-spacing: -0.04em;
    margin: 47px 0 26px;
}

.about__info-item {
    color: #FFF;
    font-weight: 400;
    letter-spacing: 0;
    font-size: 14px;
    line-height: 20px;
    max-width: 550px;
}

.about__info-item-text {
    margin: 0;
}

.about__steps {
    display: flex;
    justify-content: space-between;
    list-style: none;
    margin: 110px 0 ;
    padding: 0;
    width: 100%;
}

.about__step_backend {
    width: 228px;
}

.about__step_frontend {
    width: 912px;
}

.about__step-title {
    margin: 0 0 14px;
    padding: 9px 14px;
    color: #000;
    background-color: #303030;
    font-weight: 400;
    font-size: 14px;
    text-align: center;
}

.about__step-title_backend {
    background-color: #3DDC84;
}

.about__step-title_frontend {
    color: #FFF;
}

.about__step-text {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #8b8b8b;
}