.footer {
    max-width: 1140px;
    margin: 79px auto 0;
}

.footer__title {
    text-align: center;
    margin: 0;
    padding-bottom: 20px;
    border-bottom: solid 1px #424242;
    color: #8B8B8B;
    font-weight: 400;
    font-size: 13px;
}

.footer__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer__copyright {
    margin: 20px 0;
    color: white;
    font-weight: 400;
    font-size: 13px;
}

.footer__items {
    margin: 20px 0;
    padding: 0;
    display: flex;
    gap: 20px;
    list-style: none;
}

.footer__link {
    text-decoration: none;
    color: white;
    font-weight: 400;
    font-size: 13px;
    transition: opacity 0.1s ease-in;
}

.footer__link:hover {
    opacity: 0.7;
}