.card-container {
    max-width: 360px;
    display: flex;
    flex-direction: column;
    margin: 0 30px 60px 0;
    position: relative;
}

.card-container:nth-child(3n) {
    margin-right: 0;
}

.card-container:hover .card-container__save-button {
    visibility: visible;
    opacity: 1;
}

.card-container__save-button {
    position: absolute;
    margin: 0;
    background-color: #313131;
    border-radius: 20px;
    color: #FFF;
    font-size: 10px;
    font-weight: 400;
    line-height: 10px;
    padding: 6px 7px;
    top: 14px;
    right: 14px;
    visibility: hidden;
    transition: visibility .1s ease-in;
    transition: opacity .15s ease-in;
    opacity: 0;
    cursor: pointer;
}

.card-container__image {
    width: 100%;
    height: 201px;
    cursor: pointer;
}

.card-container__footer {
    display: flex;
    justify-content: space-between;
    margin: 14px 14px 0;
}

.card-container__movie-title {
    color: #FFF;
    font-size: 13px;
    font-weight: 500;
    margin: 0;
}

.card-container__movie-time {
    color: #8B8B8B;
    font-size: 11px;
    font-weight: 400;
    line-height: 10px;
    padding: 3px 4px;
    background-color: #2F2F2F;
    border-radius: 3px;
    text-align: center;
    margin: 0;
}