.portfolio {
    max-width: 1140px;
    margin: 0 auto 125px;
}

.portfolio__title {
    margin: 0;
    text-align: left;
    color: #8B8B8B;
    font-weight: 400;
    font-size: 18px;
}

.portfolio__items {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 0;
    margin: 50px 0 0 0;
}

.portfolio__item {
    border-bottom: solid 1px #424242;
    padding-bottom: 19px;
    transition: opacity 0.1s ease-in;
}

.portfolio__item:hover {
    opacity: 0.4;
}

.portfolio__item:last-of-type {
    border-bottom: none;
}

.portfolio__link {
    text-decoration: none;
    color: #FFF;
    text-align: left;
    font-weight: 400;
    font-size: 30px;
    letter-spacing: -0.04em;
    line-height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.portfolio__link::after {
    content: '';
    background-image: url('../../images/portfolio-img.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 18px;
    height: 18px;
}