.promo {
    display: flex;
    margin: 0 auto;
    max-width: 1200px;
    min-height: 427px;
    background-color: #272727;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  
  .promo__title {
    text-align: center;
    max-width: 730px;
    font-weight: 400;
    font-size: 50px;
    line-height: 58px;
    letter-spacing: -0.04em;
    margin: 160px 235px 151px;
    color: #FFF;
  }