.techs {
max-width: 100%;
background-color: #272727;
}

.techs__content {
    max-width: 1140px;
    min-height: 625px;
    margin: 0 auto;
}

.techs__title {
    margin: 0;
    padding: 100px 0 23px;
    text-align: left;
    border-bottom: solid 1px white;
    color: #FFF;
    font-weight: 400;
    font-size: 22px;
    letter-spacing: -0.04em;
}

.techs__subtitle {
    text-align: center;
    margin: 90px 0 26px;
    font-weight: 400;
    letter-spacing: -0.04em;
    font-size: 50px;
    color: #FFF;
}

.techs__text {
    text-align: center;
    max-width: 460px;
    margin: 0 auto 100px;
    font-weight: 400;
    letter-spacing: -0.04em;
    line-height: 20px;
    font-size: 14px;
    color: #FFF;
}

.techs__items {
    gap: 10px;
    display: grid;
    grid-template-columns: repeat(auto-fit, 90px);
    justify-content: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
}

.techs__item {
    height: 60px;
    background: #303030;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: -0.04em;
    font-weight: 400;
    letter-spacing: -0.04em;
    font-size: 14px;
    color: #FFF;
}