.about-me {
    max-width: 1140px;
    margin: 0 auto 100px;
}

.about-me__title {
    margin: 110px auto 66px;
    padding: 0 0 23px;
    text-align: left;
    border-bottom: solid 1px white;
    color: #FFF;
    font-weight: 400;
    font-size: 22px;
    letter-spacing: -0.04em;
}

.about-me__info {
    display: flex;
    justify-content: space-between;
}

.about-me__description {
    max-width: 600px;
}

.about-me__name {
    color: #FFF;
    font-weight: 400;
    font-size: 50px;
    line-height: 58px;
    letter-spacing: -0.04em;
    text-align: left;
    margin: 0 0 18px;
}

.about-me__job {
    margin: 0 0 26px;
    color: #FFF;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    text-align: left;
}

.about-me__text {
    margin: 0 0 99px;
    color: #FFF;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    text-align: left;
}

.about-me__gh-link {
    text-decoration: none;
    color: #FFF;
    font-weight: 500;
    font-size: 14px;
    text-align: left;
    transition: opacity 0.1s ease-in;
}

.about-me__gh-link:hover {
    opacity: 0.7;
}

.about-me__photo-link {
    width: 270px;
    height: 327px;
}

.about-me__photo {
    width: 270px;
    height: 327px;
    border-radius: 10px;
    object-fit: cover;
    object-position: center;
    transition: opacity 0.1s ease-in;
}

.about-me__photo:hover {
    opacity: 0.5;
}