.movies-header__logo {
    width: 38px;
    height: 38px;
    background: url(../../images/header-logo.svg);
}

.movies-header {
    max-width: 1280px;
    min-height: 74px;
    margin: 18px 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.movies-header_inactive {
    display: none;
}

.movies-header__nav-items {
    list-style: none;
    display: flex;
    justify-content: space-between;
    max-width: 212px;
    margin: 0 364px 0 426px;
    padding: 0;
    gap: 16px;
}

.movies-header__link_active {
    text-decoration: none;
    color: #ffffff8c;
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
}

.movies-header__link {
    text-decoration: none;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    transition: opacity 0.1s ease-in;
}

.movies-header__link:hover {
    opacity: 0.7;
}

.movies-header__profile-button {
    text-decoration: none;
    color: #FFF;
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    background-color: #313131;
    padding: 8px 20px;
    border-radius: 20px;
    transition: opacity 0.1s ease-in;
}

.movies-header__profile-button_active {
    color: #ffffff8c;
    text-decoration: none;
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    background-color: #313131;
    padding: 8px 20px;
    border-radius: 20px;
}

.movies-header__profile-button:hover {
    opacity: 0.7;
}