.header {
    max-width: 1280px;
    min-height: 74px;
    margin: 21px 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header_inactive {
    display: none;
}

.header__logo {
    width: 38px;
    height: 38px;
    background: url(../../images/header-logo.svg);
}

.header__auth-buttons {
    display: flex;
    align-items: center;
    column-gap: 30px;
    margin: 0;
    padding: 0;
    list-style: none;
}

.header__auth-button {
    text-decoration: none;
    color: #FFF;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0;
    font-size: 12px;
    transition: opacity 0.1s ease-in;
}

.header__auth-button:hover {
    opacity: 0.7;
}

.header__auth-button_green-theme {
    color: #000;
    background-color: #3DDC84;
    padding: 8px 20px;
    border-radius: 3px;
}