.nav {
    max-width: 100%;
    display: flex;
    justify-content: center;
    max-width: 1200px;
    min-height: 66px;
    background-color: #272727;
    margin: 0 auto;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.nav__items {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.nav__item {
    text-decoration: none;
    color: #FFF;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0;
    font-size: 12px;
    background-color: #303030;
    padding: 10px 17px;
    border-radius: 10px;
    transition: opacity 0.1s ease-in;
}

.nav__item:hover {
    opacity: 0.7;
}